<template>
  <div>
    <button
      v-if="sstatus_mikey === 401"
      @click="confirmShopClosure(402, '영업을 종료하시겠습니까?')"
      class="status-button"
    >
      운영종료하기
    </button>
    <button
      v-if="sstatus_mikey === 402"
      @click="confirmShopClosure(401, '영업을 시작하시겠습니까?')"
      class="status-button"
    >
      운영시작하기
    </button>
    <span v-if="sstatus_mikey === 402">(영업종료)</span>
  </div>
</template>


<script>
import axios from "axios";

export default {
  name: "B_ShopStatus",
  props: ["bnum"],
  data() {
    return {
      sstatus_mikey: null,
    };
  },
  created() {
    // 컴포넌트가 생성될 때 운영 상태 값을 가져옵니다.
    this.fetchShopStatus();
  },
  methods: {
    async fetchShopStatus() {
      try {
        // 서버에 GET 요청을 보내 운영 상태 값을 가져옵니다.
        const response = await axios.get("/ShopStatus", {
          params: { bnum: this.bnum }, // bnum을 쿼리 파라미터로 전달합니다.
        });

        // 응답 데이터에서 운영 상태 값을 설정합니다.
        this.sstatus_mikey = response.data.sstatus_mikey;
        console.log("Fetch Shop Status Response:", response.data); // 응답 데이터 확인
      } catch (error) {
        console.error("운영 상태 값을 가져오는 중 오류 발생:", error);
      }
    },
    // async confirmShopClosure(newStatus, message) {
    //   // 확인 메시지 표시
    //   if (confirm(message)) {
    //     console.log("새로운 상태:", newStatus); // 새로운 상태 값 확인
    //     try {
    //       // 서버에 POST 요청을 보내 상태 값을 업데이트합니다.
    //       const response = await axios.post("http://localhost:8888/ShopStatusUpdate", {
    //         sstatus_mikey: newStatus,
    //         bnum: this.bnum,
    //       });
    //       // 응답에서 sstatus_mikey를 가져옵니다.
    //       const updatedStatus = response.data.sstatus_mikey;
    //       console.log("setStatusMikey Response:", updatedStatus);
    //       // 상태 값 업데이트 후 다시 운영 상태 값을 가져옵니다.
    //       this.sstatus_mikey = updatedStatus; // 이 부분이 추가되었습니다.

    //     } catch (error) {
    //       console.error("운영 상태 값을 업데이트하는 중 오류 발생:", error);
    //     }
    //   }
    // }
  },
};
</script>


