<template>
  <div class="container" style="max-height: 500px; overflow-y: auto">
    <div class="row">
      <div
        v-for="(order, index) in uniqueOrders"
        :key="index"
        class="col-3 mb-3"
      >
        <div class="card mt-3" style="width: 18rem">
          <h5 class="card-title">이미지</h5>
          <div class="card-body">
            <div class="card" style="width: 16rem">
              <ul class="list-group list-group-flush">
                <li class="list-group-item">
                  주문일시: {{ formatDate(order.odate) }}
                </li>
                <li class="list-group-item">주문번호: {{ order.osnum }}</li>
                <li class="list-group-item">사업자번호: {{ order.bnum }}</li>
              </ul>
            </div>
            <p></p>
            <div class="button-group">
              <button
                type="button"
                v-if="order.ostatus_mikey === 802"
                @click="setOstatusMikey(order)"
              >
                확인
              </button>
              <button
                type="button"
                v-if="order.ostatus_mikey === 803"
                @click="setOstatusMikey(order)"
              >
                조리중
              </button>
              <button
                type="button"
                v-if="order.ostatus_mikey === 804"
                @click="setOstatusMikey(order)"
              >
                배달시작
              </button>
              <button
                type="button"
                v-if="order.ostatus_mikey === 805"
                @click="setOstatusMikey(order)"
              >
                배달완료
              </button>
              <button
                type="button"
                class="btn"
                @click="showOrderDetailModal = true"
              >
                상세보기
              </button>
            </div>
            <MS_B_OrderDetailModal
              v-if="showOrderDetailModal"
              @close="showOrderDetailModal = false"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import MS_B_OrderDetailModal from "./MS_B_OrderDetailModal.vue";

export default {
  name: "B_OrdSpecCard",
  props: ["orderStatus", "bnum"], // bnum 추가
  components: {
    MS_B_OrderDetailModal,
  },

  data() {
    return {
      orderList: [],
      showOrderDetailModal: false,
    };
  },
  mounted() {
    this.fetchOrderList();
  },
  computed: {
    uniqueOrders() {
      const uniqueOrdersMap = {};
      this.filteredOrders.forEach((order) => {
        if (!uniqueOrdersMap[order.osnum]) {
          uniqueOrdersMap[order.osnum] = order;
        }
      });
      return Object.values(uniqueOrdersMap);
    },
    filteredOrders() {
      if (!this.orderStatus || this.orderStatus.length === 0) {
        return [];
      }
      // 주문 상태에 따라 주문 필터링
      return this.orderList.filter(
        (order) =>
          order.ostatus_mikey === this.orderStatus && order.bnum === this.bnum
      );
    },
  },
  methods: {
    formatDate(date) {
      const formattedDate = new Date(date);
      return formattedDate.toLocaleString();
    },
    openModal() {
      this.showOrderDetailModal = true;
    },
    async fetchOrderList() {
      try {
        const response = await axios.get("/B_Ordering");
        this.orderList = response.data.sort(
          (a, b) => new Date(b.odate) - new Date(a.odate)
        ); // 주문일시를 최신순으로 정렬
        console.log("Response:", response.data);
      } catch (error) {
        console.error("Error fetching orderList:", error);
      }
    },
    async setOstatusMikey(order) {
      try {
        let newStatus;
        if (order.ostatus_mikey === 803) {
          newStatus = 804; // 조리중(803)을 누르면 배달시작(804)로 값을 변경
        } else if (order.ostatus_mikey === 804) {
          newStatus = 805; // 배달시작(804)을 누르면 배달완료(805)로 값을 변경
        } else {
          newStatus = 803; // 그 외의 경우에는 기존처럼 803으로 값을 변경
        }
        const response = await axios.post("/StatusUpdate", {
          ostatus_mikey: newStatus,
          bnum: order.bnum,
          onum: order.onum,
        });
        console.log("Response:", response.data);
        this.fetchOrderList(); // 주문 상태를 업데이트한 후 주문 목록을 다시 가져옵니다.
      } catch (error) {
        console.error("ostatus_mikey 값을 업데이트하는 중 오류 발생:", error);
      }
    },
  },
};
</script>

